.footer {
  position: relative;
  background-color: #ffffff;
  border-radius: 30px 30px 0 0;
  z-index: 3;
  &__wrapper{
    position: relative;
    display: flex;
    flex-direction: column;
    width: 320px;
    height: 231px;
    margin: 0 auto;
    margin-top: -37px;
    margin-bottom: 8px;
    padding: 0 25px;
  }
}
.footer__logo {
  position: absolute;
  top: 38px;
  left: 110px;
  width: 200px;
  height: 19px;
  background-image: url("../img/logo-mobile-blue@1x.png");
  background-repeat: no-repeat;
}

.footer__social {
  display: flex;
  position: absolute;
  left: 76px;
  top: 78px;
}

.footer__social::after {
  content: "";
  display: block;
  position: absolute;
  top: 82px;
  left: -51px;
  width: 270px;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.1);
}

.footer__social-item {
  display: flex;
  margin-right: 45px;
  justify-content: space-around;
}

.footer__social-facebook {
  position: absolute;
  background-image: url("../img/facebook.svg");
  background-size: 10px;
  background-position: 16px 13px;
  background-repeat: no-repeat;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  background-color: #ffd74b;
}

.footer__social-vk {
  position: absolute;
  background-image: url("../img/vk.svg");
  background-size: 25px;
  background-position: 9px 16px;
  background-repeat: no-repeat;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  background-color: #ffd74b;
}

.footer__social-instagram {
  position: absolute;
  background-image: url("../img/instagram.svg");
  background-size: 18px;
  background-position: 13px 13px;
  background-repeat: no-repeat;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  background-color: #ffd74b;
}

.footer__link {
  color: #444444;
}

.footer__dev {
  position: absolute;
  top: 195px;
  left: 26px;
}

.footer__logo-academy {
  position: absolute;
  top: 188px;
  right: 19px;
  width: 40px;
}
@media (min-width: $tablet-width) {
  .footer {
    border-radius: 60px 60px 0 0;
    &__wrapper{
     width: 768px;
     height: 147px;
     margin-top: 0px;

   }
 }

 .footer__logo {
  top: 56px;
  left: 40px;
  height: 40px;
  background-image: url(../img/logo-tablet-blue@1x.png);
}

.footer__social {
 left: 284px;
 top: 32px;
}

.footer__social::after {
  display: none;
}

.footer__social-item {
  margin-right: 60px;
}

.footer__dev {
  position: absolute;
  top: 63px;
  left: 574px;
  color: #444444;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
}

.footer__social-facebook{
 width: 60px;
 height: 60px;
 background-size: 16px;
 background-position: 22px 15px
}

.footer__social-vk {
 width: 60px;
 height: 60px;
 background-size: 32px;
 background-position: 13px 22px;
}

.footer__social-instagram {
  width: 60px;
  height: 60px;
  background-size: 24px;
  background-position: 18px 18px;
}

.footer__logo-academy {
  top: 59px;
  right: 33px;
  width: 40px;
}

}
@media (min-width: $desktop-width) {
  .footer {
    border-radius: 60px 60px 0 0;
    &__wrapper {
      flex-direction: row;
      width:1320px;
      margin: 0 60px;
    }
  }

  .footer__logo {
    top: 51px;
    left: 110px;
    height: 50px;;
    background-image: url(../img/logo-desktop-blue@1x.png);
  }

  .footer__social {
    left: 669px;
  }

  .footer__dev {
   left: 1276px;
 }

 .footer__logo-academy {
  right: -116px;
}
}

